








export default {
	name: 'uni-segmented-control',
	props: {
		current: {
			type: Number,
			default: 0
		},
		values: {
			type: Array,
			default () {
				return [];
			}
		},
		activeColor: {
			type: String,
			default: '#007aff'
		},
		styleType: {
			type: String,
			default: 'button'
		}
	},
	data() {
		return {
			currentIndex: this.current
		}
	},
	watch: {
		current(val) {
			if (val !== this.currentIndex) {
				this.currentIndex = val;
			}
		}
	},
	computed: {
		wrapStyle() {
			let styleString = '';
			switch (this.styleType) {
				case 'text':
					styleString = `border:0;`;
					break;
				default:
					styleString = `border-color: ${this.activeColor}`;
					break;
			}
			return styleString;
		},
		itemStyle() {
			let styleString = '';
			switch (this.styleType) {
				case 'text':
					styleString = `color:#000;border-left:0;`;
					break;
				default:
					styleString = `color:${this.activeColor};border-color:${this.activeColor};`;
					break;
			}
			return styleString;
		},
		activeStyle() {
			let styleString = '';
			switch (this.styleType) {
				case 'text':
					styleString = `color:${this.activeColor};border-left:0;border-bottom-style:solid;`;
					break;
				default:
					styleString = `color:#fff;border-color:${this.activeColor};background-color:${this.activeColor}`;
					break;
			}
			return styleString;
		}
	},
	methods: {
		onClick(index) {
			if (this.currentIndex !== index) {
				this.currentIndex = index;
				this.$emit('clickItem', index);
			}
		}
	},
}
